<template>
  <div class="box" :style="{width:width,height:height}">
    <div
      class="ball"
      @touchstart="ballStart($event)"
      @touchmove="ballMove($event)"
      @touchend="ballEnd($event)"
      ref="ball"
    ></div>
  </div>
</template>

<script>
export default {
  name: "Home",

  data() {
    return {
      startX: 0,
      startY: 0,
      moveX: 0,
      moveY: 0,
      endX: 0,
      endY: 0,
      x: 0,
      y: 0,
      width:0,
      height:0,
      // a: 0,
      differX: 0,
      differY: 0
    };
  },
  methods: {
    ballStart(e) {
 
 
      // let ball = e.touches[0].target;
 
      this.startX = e.touches[0].pageX;
      this.startY = e.touches[0].pageY;
      this.width = window.innerWidth;
      this.height = window.innerHeight;
      // this.$refs.ball.style.transition = null;
    },
    ballMove(e) {
      // 获取相关尺寸 位置
      let ballSize = this.$refs.ball.offsetWidth;
      let maxX = window.innerWidth - ballSize;
      let maxY = window.innerHeight - ballSize;
 

      // 移动中取消动画效果
      this.$refs.ball.style.transition = null;
      // this.a = e.touches[0];
 
 
      this.moveX = e.touches[0].pageX - this.startX + this.endX;
      this.moveY = e.touches[0].pageY - this.startY + this.endY;
      this.moveX = this.moveX >= maxX ? maxX : this.moveX;
      this.moveX = this.moveX <= 0 ? 0 : this.moveX;
      this.moveY = this.moveY <= 0 ? 0 : this.moveY;
      this.moveY = this.moveY >= maxY ? maxY : this.moveY;
      this.$refs.ball.style.left = this.moveX + "px";
      this.$refs.ball.style.top = this.moveY + "px";
      // this.$refs.ball.style.transform = `translateX(${this.moveX}px) translateY(${this.moveY}px)`;
      this.differX = e.touches[0].pageX - this.$refs.ball.offsetLeft;
      this.differY = e.touches[0].pageY - this.$refs.ball.offsetTop;
      this.x = e.touches[0].pageX - this.differX;
      this.y = e.touches[0].pageY - this.differY;
 
 
    },
    ballEnd() {
      // 记录结束值
      this.endX = this.x;
      this.endY = this.y;
      // 屏幕尺寸
      let screenW = window.innerWidth;
      let screenH = window.innerHeight;
      // 中线
      let halfWidth = screenW / 2;
      let halfHeight = screenH / 2;
      // 悬浮球尺寸
      let ballSize = this.$refs.ball.offsetWidth;
      // 悬浮球边框距离
      let ballLeft = this.$refs.ball.offsetLeft;
      let ballTop = this.$refs.ball.offsetTop;
      // 移动最大范围
      let maxX = screenW - ballSize;
      let maxY = screenH - ballSize;

 

      let left = ballLeft + ballSize / 2;
      let top = ballTop + ballSize / 2;
      // let offsetTop = ballTop;
      let offsetRight = screenW - ballLeft - ballSize;
      let offsetBottom = screenH - ballTop - ballSize;
 

 

 
 
 
 

 

      // this.endX = this.moveX;
      // this.endY = this.moveY;
 
 

      if (left < halfWidth) {
        // this.$refs.ball.style.transform = "translateX(0px) translateY("+this.y+"px)";
        // this.$refs.ball.style.transform = "translateY("+this.y+"px)";
        if (top > halfHeight) {
          if (ballLeft > offsetBottom) {
            this.$refs.ball.style.top = maxY + "px";
            this.$refs.ball.style.left = this.x + "px";
            this.endY = maxY;
          } else {
            this.$refs.ball.style.left = "0px";
            this.$refs.ball.style.top = this.y + "px";
            this.endX = 0;
          }
        } else {
          if (left > top) {
            this.$refs.ball.style.left = this.x + "px";
            this.$refs.ball.style.top = "0px";
            this.endY = 0;
          } else {
            this.$refs.ball.style.left = "0px";
            this.$refs.ball.style.top = this.y + "px";
            this.endX = 0;
          }
        }
      } else {
        if (top > halfHeight) {
          if (offsetRight > offsetBottom) {
            this.$refs.ball.style.left = this.x + "px";
            this.$refs.ball.style.top = maxY + "px";
            this.endY = maxY;
          } else {
            this.$refs.ball.style.left = maxX + "px";
            this.$refs.ball.style.top = this.y + "px";
            this.endX = maxX;
          }
        } else {
          if (offsetRight > ballTop) {
            this.$refs.ball.style.left = this.x + "px";
            this.$refs.ball.style.top = "0px";
            this.endY = 0;
          } else {
            this.$refs.ball.style.left = maxX + "px";
            this.$refs.ball.style.top = this.y + "px";
            this.endX = maxX;
          }
        }
      }
      this.$refs.ball.style.transition = "all 0.5s ease-in-out";
    }
  }
};
</script>

<style lang="less" scoped>
html,
body {
  padding: 0;
  margin: 0;
}
.box {
  width: 100%;
  height: 100%;
  .ball {
    position: fixed;
    top: 0;
    left: 0;
    width: 60px;
    height: 60px;
    // background-color: slateblue;
    background-color: slateblue;
    // background-color: rgba(0, 0, 0, 0.5);
    background-image: radial-gradient(steelblue, slateblue, lightblue, #ccc);
    // transform: translate();
    // transition: all 0.5s ease-in-out;
    border-radius: 16%;
    // box-shadow: 0 3px 3px #333;
  }
}
</style>
